<template>
  <div class="me">
    <van-nav-bar title="" :left-text="str" :left-arrow="flag" @click-left="onClickLeft"/>

    <router-view/>

    <van-tabbar v-model="active">
      <van-tabbar-item icon="wap-home" to="/"></van-tabbar-item>
      <van-tabbar-item icon="label-o" to="/category"></van-tabbar-item>
      <van-tabbar-item icon="search" to="/news"></van-tabbar-item>
      <van-tabbar-item icon="shopping-cart" to="/shopcar"></van-tabbar-item>
      <van-tabbar-item icon="contact" to="/me"></van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 4,
      flag: false,
      str: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  watch: {
    "$route.path": function(newVal, oldVal) {
      if (
        newVal === "/me/pwdReset" ||
        newVal === "/me/addressList" ||
        newVal === "/me/myorder"
      ) {
        this.flag = true;
        this.str = "";
      }
    }
  }
};
</script>

<style lang="less" scoped>
.me {
  padding-bottom: 50px;
}
</style>

