<template>
  <div class="register">
    <van-cell-group>
      <van-field v-model="receiver_name" required clearable label="" placeholder=""/>
      <van-field v-model="mobile" required clearable label="" placeholder=""/>
      <van-field v-model="postcode" label="" placeholder="" required/>
      <van-field
        v-model="pro_city_area"
        label=""
        placeholder="//"
        required
        @focus="show=true"
      />
      <van-field v-model="address" label="" placeholder="" required/>
    </van-cell-group>
    <!--  -->
    <van-popup v-model="show" position="bottom" :overlay="false">
      <van-area :area-list="areaList" @confirm="confirm" @cancel="cancel"/>
    </van-popup>

    <van-button type="warning" size="large" @click="addressAdd"></van-button>
  </div>
</template>

<script>
import qs from 'qs'
import areaList from '../../assets/address.js'
export default {
    data(){
        return {
           receiver_name:"",
           mobile:"",
           postcode:"",
           pro_city_area:"",
           show:false,  //
           province:"",
           city:"",
           area:"",
           address:"",
           areaList:areaList
        }
    },
    created(){
       
    },
    methods:{
        confirm(data){
            //    console.log(data);
            this.province = data[0].name;
            this.city = data[1].name;
            this.area = data[2].name;
            this.pro_city_area = this.province+' '+this.city+' '+this.area;
            this.show = false;
        },
        cancel(){
            this.show = false;
        },
        addressAdd(){
            const addressInfo = {
                "receiver_name": this.receiver_name,
                "mobile": this.mobile,
                "postcode": this.postcode,
                "province": this.province,
                "city": this.city,
                "area": this.area,
                "detailed_address": this.address,
            }

            const addressStr = qs.stringify(addressInfo)
            this.$http.post("/v1/users/addReceiverAddress",addressStr).then(res=>{
                if(res.data.status==200){
                    this.$toast('')
                    setTimeout(()=>{
                        this.$router.push('/me/addressList');
                    },1000) 
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
button {
  margin-top: 50px;
  width: 80%;
}
</style>

