<template>
  <div class="newsInfo">
    <van-nav-bar fixed :title="newsInfo.title" left-text="" left-arrow @click-left="onClickLeft"/>
    <div class="header">
      <div class="subtitle">
        <span class="ctime">:{{newsInfo.ctime}}</span>
        <span class="views">:{{newsInfo.views}}</span>
      </div>
    </div>

    <div class="body" v-html="newsInfo.content"></div>

    <comment :id="id"></comment>
  </div>
</template>

<script>
import comment from "../components/comment.vue";
export default {
  data() {
    return {
      id: this.$route.params.id,
      newsInfo: {}
    };
  },
  created() {
    this.$http.get("/v1/news/getNewsInfo/" + this.id).then(res => {
      if (res.data.status == 200) {
        this.newsInfo = res.data.data;
      }
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  components: {
    comment
  }
};
</script>

<style lang="less">
.newsInfo {
  .header {
    width: 100%;
    // height:50px;
    border-bottom: 1px solid #ccc;
    padding: 5px;
    .title {
      color: red;
      font-weight: bold;
      font-size: 16px;
    }
    .subtitle {
      display: flex;
      justify-content: space-between;
      padding: 10px;
      font-size: 12px;
    }
  }
  .body {
    padding: 10px;
    margin-top: 20px;
    font-size: 14px;
    border-bottom: 1px solid #ccc;
    line-height: 2em;
    text-align: left;
    img {
      width: 100%;
    }
  }
}
</style>

