<template>
    <div>
        <van-cell-group>
        <van-field
            v-model="oldPwd"
            type="password"
            label=""
            placeholder=""
            required
        />
        <van-field
            v-model="newPwd"
            type="password"
            label=""
            placeholder=""
            required
        />
    </van-cell-group>
     <van-button type="warning" size="large" @click="changePwd"></van-button>
    </div>
</template>

<script>
import qs from 'qs'
export default {
    data() {
        return {
            oldPwd:'',
            newPwd:'',
        };
    },
    methods: {
        changePwd(){
          const pwd = {
              oldPassword:this.oldPwd,
              newPassword:this.newPwd
          }
          const pwdStr = qs.stringify(pwd);
          this.$http.post("/v1/users/updatePassword",pwdStr).then(res=>{
            if(res.data.status==200){
                this.$toast.success('');
                setTimeout(()=>{
                    this.$router.push('/me/list');
                },1000) 
            }else{
                 this.$toast.fail('');
            }
          })
       }
    }
}
</script>

<style lang="less" scoped>
    button{
        margin:10px 20px;
        width:90%;
        border-radius: 8px;
    }
</style>
