<template>
  <div class="login">
    <van-cell-group>
      <van-field
        v-model="username"
        required
        clearable
        label=""
        icon="question-o"
        placeholder=""
      />
      <van-field
        v-model="password"
        type="password"
        label=""
        clearable
        placeholder=""
        required
      />
    </van-cell-group>
    <van-button type="danger" size="large" @click="login"></van-button>
    <div class="reg"> ?
      <a href="javascript:;" @click="register"></a>
    </div>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    register() {
      this.$router.push("/me/register");
    },
    login() {
      const userInfo = {
        username: this.username,
        password: this.password
      };
      const userStr = qs.stringify(userInfo);
      this.$http.post("/v1/users/login", userStr).then(res => {
        if (res.data.status == 200) {
          this.$toast.success("");
          localStorage.setItem("token", res.data.data.token);
          setTimeout(() => {
            this.$router.push("/me/list");
          }, 1000);
        } else {
          this.$toast.fail(",");
        }
      });
    }
  }
};
</script>

<style lang="less">
.login {
  .van-cell {
    height: 50px;
    line-height: 30px;
  }
  .van-cell__title,
  .van-cell__value {
    font-size: 18px;
  }
  button {
    width: 80%;
    margin-top: 30px;
  }
  .reg {
    margin: 30px;
    text-align: right;
    padding-right: 30px;
    font-size: 20px;
    a {
      font-size: 24px;
      color: blue;
    }
  }
}
</style>

